// Breakpoints Min-Width
@mixin breakpoint($point) {
    @if $point==desktop_large {
        @media (min-width: $w_desktop_large) {
            @content;
        }
    }
    @if $point==desktop {
        @media (min-width: $w_desktop) {
            @content;
        }
    }
    @else if $point==tablet {
        @media (min-width: $w_tablet) {
            @content;
        }
    }
    @else if $point==phablet_large {
        @media (min-width: $w_phablet_large) {
            @content;
        }
    }
    @else if $point==phablet {
        @media (min-width: $w_phablet) {
            @content;
        }
    }
    @else if $point==phablet_small {
        @media (min-width: $w_phablet_small) {
            @content;
        }
    }
    @else if $point==mobile {
        @media (min-width: $w_mobile) {
            @content;
        }
    }
    @else if $point==mobile_small {
        @media (min-width: $w_mobile_small) {
            @content;
        }
    }
}

// Breakpoints Max-Width
@mixin mq($point) {
    @if $point==desktop {
        @media (max-width: $w_desktop) {
            @content;
        }
    }
    @else if $point==tablet {
        @media (max-width: $w_tablet_mq) {
            @content;
        }
    }
    @else if $point==phablet {
        @media (max-width: $w_phablet) {
            @content;
        }
    }
    @else if $point==mobile {
        @media (max-width: $w_mobile) {
            @content;
        }
    }
}